import React, { useEffect, useState } from "react";
import { TextLabel } from "@findxdn/erp-theme";
import { CustomDateTimePickerX, TextInput, CustomAutocomplete } from "../../../components/CustomMUI/ListCustomMui";
import { useForm, Controller } from "react-hook-form";
import styles from './CreateRoomController.module.scss'
import Grid from '@mui/material/Grid';
import {
    Button,
  } from '@findxdn/erp-theme';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import LoginAction from "../../../redux/login/action";
import AddressAction from "../../../redux/address/action";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import Utils from "../../../utils/Utils";
import * as RouterPath from "../../../router/RouterPath"
import useRouter from "../../../hooks/use-router";
import getErrorMessage from "../../../utils/ErrorConstant";
import CinemaRoomAction from "../../../redux/cimemaroom/action";

function CreateRoomController()
{
    const {
        handleSubmit,
        control,
        setValue,
        formState: { errors },
        watch,
        reset
    } = useForm();
    const router= useRouter()

    const [gender, setGender] = React.useState(0);

    const dispatch = useDispatch()

    const [listBranch, setListBranch] = useState([])

    const CreateRoom = (dataSubmit) => {
        dispatch({
            type: CinemaRoomAction.CREATE_CINEMAROOM,
            data: dataSubmit,
            onSuccess: (data) => {
                console.log(data)
                Utils.showSuccessToast({
                  message: "To phng chiu thnh cng",
                });
                router.push({
                  pathname: RouterPath.ROOM_LIST,
              })
            },
            onError: (data) => {
                Utils.showErrorToast({
                  message:
                    `To phng chiu tht bi: ${getErrorMessage(data)}`,
                });
              },
        })
    }
 
    const getBranch = (dataSubmit) => {
        dispatch({
            type: AddressAction.GET_BRANCH,
            onSuccess: (data) => {
                setListBranch(data?.map(item=>({ key: item?.id, label: item?.name})))
            },
        })
    }

    useEffect(() => {
        getBranch()
    }, [])
    
    const onReset = () => {
        reset({
          name: '',
          branch: null,
          position: null,
          dateStart: null
        });
      };

    const onSubmit = (data) => {
        const dataSubmmit = {
            name: data?.name,
            totalColumn: data?.totalColumn,
            totalRow: data?.totalRow,
            totalSeat: data?.totalSeat,
            branchId: data?.branchId,
          }
        CreateRoom(dataSubmmit)
    }

    return (
        <div 
            className={styles.showing}
        >
            <form
                onSubmit={handleSubmit(onSubmit)}
                style={{ display: "flex", flexDirection: "column", maxWidth: "900xp", padding: '40px 80px 20px', gap: '20px' }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={3} md={3} xl={3}>
                        <TextLabel>
                            Tn phng chiu<p style={{ margin: 0 }}>*</p>
                        </TextLabel>
                        <Controller
                            control={control}
                            rules={{
                                required: true,
                            }}
                            render={({ field: { ref, ...rest } }) => (
                                <TextInput
                                    {...rest}
                                    disablePortal
                                    placeholder='Nhp tn phng chiu'
                                    variant="outlined"
                                    InputLabelProps={{ style: { fontSize: 13 } }}
                                    sx={{ width: "100px" }}
                                />
                            )}
                        name="name"
                        />
                        {errors?.name?.type === "required" && (
                        <p style={{ color: "red", marginBottom: 0 }}>
                            Vui lng nhp tn phng chiu!{" "}
                        </p>
                        )}
                    </Grid>
                    <Grid item xs={3} md={3} xl={3}>
                        <TextLabel>
                            S dy phng chiu<p style={{ margin: 0 }}>*</p>
                        </TextLabel>
                        <Controller
                            control={control}
                            rules={{
                                required: true,
                            }}
                            render={({ field: { ref, ...rest } }) => (
                                <TextInput
                                    {...rest}
                                    disablePortal
                                    placeholder='S dy phng chiu'
                                    variant="outlined"
                                    InputLabelProps={{ style: { fontSize: 13 } }}
                                    sx={{ width: "100px" }}
                                    type="number"
                                />
                            )}
                        name="totalRow"
                        />
                        {errors?.totalRow?.type === "required" && (
                        <p style={{ color: "red", marginBottom: 0 }}>
                            Vui lng nhp s hng phng chiu!{" "}
                        </p>
                        )}
                    </Grid>
                    <Grid item xs={3} md={3} xl={3}>
                        <TextLabel>
                            S ct phng chiu<p style={{ margin: 0 }}>*</p>
                        </TextLabel>
                        <Controller
                            control={control}
                            rules={{
                                required: true,
                            }}
                            render={({ field: { ref, ...rest } }) => (
                                <TextInput
                                    {...rest}
                                    disablePortal
                                    placeholder='S ct phng chiu'
                                    variant="outlined"
                                    InputLabelProps={{ style: { fontSize: 13 } }}
                                    sx={{ width: "100px" }}
                                    type="number"
                                />
                            )}
                        name="totalColumn"
                        />
                        {errors?.totalColumn?.type === "required" && (
                        <p style={{ color: "red", marginBottom: 0 }}>
                            Vui lng nhp s ct phng chiu!{" "}
                        </p>
                        )}
                    </Grid>
                    <Grid item xs={3} md={3} xl={3}>
                        <TextLabel>Chi nhnh<p style={{ margin: 0 }}>*</p></TextLabel>
                        <Controller
                        control={control}
                        rules={{
                            required: true,
                        }}
                        render={({ field: { ref, ...rest } }) => (
                            <CustomAutocomplete
                                {...rest}
                                placeholder="Chn chi nhnh"
                                variant="outlined"
                                options={listBranch}
                                InputLabelProps={{ style: { fontSize: 13 } }}
                                sx={{ width: "300px" }}
                            />
                        )}
                        name="branchId"
                        />
                        {errors?.branchId?.type === "required" && (
                        <p style={{ color: "red", marginBottom: 0 }}>
                            Vui lng chn chi nhnh!{" "}
                        </p>
                        )}
                    </Grid>
                </Grid>
                <div 
                    className="d-flex justify-content-end"
                    style={{
                        padding:'20px',
                        gap: '20px'
                    }}
                >
                    <Button
                        className="btn border-green bases__margin-right--20"
                        onClick={onReset}
                    >
                        Thit lp li
                    </Button>
                    <Button
                        className="btn background-green btn-search-style"
                        type="submit"
                    >
                        Lu
                    </Button>
                </div>
            </form>
        </div>    
    )
}
export default CreateRoomController