import { Box } from '@mui/material'
import useTrans from '../../../../hooks/use-trans';
import { useSelector } from 'react-redux'
import styles from './DetailFilmController.module.scss'
import {
    Button,
  } from '@findxdn/erp-theme';
import moment from 'moment';
import * as RouterPath from "../../../../router/RouterPath"
import useRouter from '../../../../hooks/use-router';
import { convertNumberToString } from '../../../../utils/Utils';

export default function DetailFilmController(props) {
    const router= useRouter()
    const { data } = props
    var dataValue = {}
    if(data != null && data.length != 0)
    {
        dataValue = data
    }
    const content = (name, value, isImage = false, src="") => {
        return (
            <div style={{
                display: 'flex',
                flexDirection: 'row',
                gap: '10px',
                width: '100%'
            }}>
                <div style={{ width: '40%', flex: 'left'}}>
                    {name}
                </div>
                {!isImage && <div style={{ width: '60%', flex: 'left'}}>
                    : {value}
                </div>}
                {isImage && <img src={src} width='55%' />}
            </div>    
        )
    }
    const MovieStatus = (status) => {
        let statusText;
      
        if (status === "10") {
          statusText = "Phim mi";
        } else if (status === "20") {
          statusText = "ang chiu";
        } else if (status === "30") {
          statusText = "Tm hon";
        } else if (status === "40") {
          statusText = " chiu";
        } else {
          statusText = "Trng thi khng xc nh";
        }
        return statusText
    }
    return (
        <Box
            className={styles.DetailEmployeeController}
            style={{ backgroundColor: '#fff' }}
        >
            <div>
                Thng tin chi tit phim
            </div>
            <div 
                className={styles.infoDetail}
            >
                <div className={styles.column}>
                    {content('Tn phim', dataValue?.name)} 
                    {content(' tui gii hn', dataValue?.ageLimit)} 
                    {content('Ngn ng', dataValue?.language)} 
                    {content('Ngy pht hnh', dataValue?.dateRelease != null ? moment(dataValue?.dateRelease).format('DD/MM/YYYY') : '')} 
                    {content('Ngy bt u', moment(dataValue?.dateStart).format('DD/MM/YYYY'),)} 
                    {content('Ngy kt thc', moment(dataValue?.dateEnd).format('DD/MM/YYYY'),)} 
                    {content('Gi vn', `${convertNumberToString(dataValue?.cost)}$`)} 
                    {content('Vietsub', dataValue?.isSubtittle ? "C" : "Khng")} 
                    {content('Din vin', dataValue?.director)} 
                    {content('o din', dataValue?.actor)} 
                    {content('nh ba', "", true, dataValue?.backgroundImageLink)} 
                </div>
                <div className={styles.column}>
                   
                    {content('Thi gian chiu (Pht)', dataValue?.time)}
                    {/* {content('Trng thi', MovieStatus(dataValue?.status))}  */}
                    {content('ng dn gii thiu', dataValue?.trailerLink)} 
                    {content('Gii thiu', dataValue?.introduce)} 
                    {content('Ngy tm hon', dataValue?.datePostpone != null ? moment(dataValue?.datePostpone).format('DD/MM/YYYY') : '',)} 
                    {content('Ngy bt u li', dataValue?.dateStartPostpone != null ? moment(dataValue?.dateStartPostpone).format('DD/MM/YYYY') : '',)}
                    {content('Ngy gia hn', dataValue?.dateExtend != null ? moment(dataValue?.dateExtend).format('DD/MM/YYYY') : '',)}
                    {content('L do tm hon', dataValue?.reasonPostpone)}
                    {content('L do tm hon', dataValue.ReasonExtend)}
                </div>
            </div>
            <div 
                className="d-flex justify-content-end"
                style={{
                    padding:'20px',
                    gap: '20px'
                }}
            >
                <Button
                    className="btn background-green btn-search-style"
                    onClick={(e) => {
                        router.push({
                            pathname: RouterPath.UPDATE_FILM,
                            params: {
                              code: dataValue?.id,
                            }
                        })
                    }}
                >
                    Sa thng tin phim
                </Button>
            </div>
        </Box>
    )
}