import { Box } from '@mui/material'
import useTrans from '../../../../hooks/use-trans';
import { useSelector } from 'react-redux'
import styles from './DetailEmployeeController.module.scss'
import {
    Button,
  } from '@findxdn/erp-theme';
import moment from 'moment';
import * as RouterPath from "../../../../router/RouterPath"
import useRouter from '../../../../hooks/use-router';

export default function DetailEmployeeController(props) {
    const router= useRouter()
    const { data } = props
    var dataValue = {}
    if(data != null && data.length != 0)
    {
        dataValue = data[0]
    }
    const content = (name, value) => {
        return (
            <div style={{
                display: 'flex',
                flexDirection: 'row',
                width: '100%'
            }}>
                <div style={{ width: '40%', flex: 'left'}}>
                    {name}
                </div>
                <div style={{ width: '60%', flex: 'left'}}>
                    : {value}
                </div>
            </div>    
        )
    }
    return (
        <Box
            className={styles.DetailEmployeeController}
            style={{ backgroundColor: '#fff' }}
        >
            <div>
                Thng tin c nhn
            </div>
            <div 
                className={styles.infoDetail}
            >
                <div className={styles.column}>
                    {content('Tn nhn vin', dataValue?.name)} 
                    {content('M nhn vin', dataValue?.employeeCode)} 
                    {content('Ngy sinh', moment(dataValue?.dateOfBirth).format('DD/MM/YYYY'),)} 
                    {content('Ngy bt u lm', moment(dataValue?.dateStart).format('DD/MM/YYYY'),)} 
                    {content('Gii tnh', dataValue?.gender == 'M' ? 'Nam' : dataValue?.gender == 'F' ? 'N' : 'Khc')} 
                    {content('Email', dataValue?.email)} 
                    {content('S in thoi', dataValue?.phone)} 
                </div>
                <div className={styles.column}>
                    {content('Chc v', dataValue?.positionName)} 
                    {content('Chi nhnh', dataValue?.branchName)} 
                    {content('a ch c th', dataValue?.name)} 
                    {content('Thnh ph', dataValue?.provinceName)} 
                    {content('Qun huyn', dataValue?.districtName)}
                    {content('Th x', dataValue?.communeName)} 
                </div>
            </div>
            <div 
                className="d-flex justify-content-end"
                style={{
                    padding:'20px',
                    gap: '20px'
                }}
            >
                <Button
                    className="btn background-green btn-search-style"
                    onClick={(e) => {
                        router.push({
                            pathname: RouterPath.EDIT_EMPLOYEE,
                            params: {
                              code: dataValue?.employeeCode,
                            }
                        })
                    }}
                >
                    Sa thng tin nhn vin
                </Button>
            </div>
        </Box>
    )
}